* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  width: 500px;
  border: none;
  border-radius: 10px;
  margin: 300px auto 50px;
  padding: 10px;
  font-size: 24px;
  font-weight: 500;
  display: block;
  box-shadow: 0 5px 10px #9aa0b90d, 0 15px 40px #0000001a;
}

#search-box:focus {
  outline: none;
}

h2 {
  margin: 0;
  font-size: 30px;
}

.country-list li:hover {
  transform: scale(1.03);
}

.country-list {
  margin: 0 auto;
  list-style: none;
}

.country-list li {
  width: 520px;
  border-width: 1px;
  border-color: gray;
  margin: 0 auto;
  padding-bottom: 10px;
  font-size: 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 14px 5px -5px #0a4e8373;
}

@media screen and (max-width: 700px) {
  .country-list li, .country-card, #search-box {
    width: 360px;
    padding: 5px;
    font-size: 14px;
  }

  #search-box {
    margin: 100px auto 50px;
  }

  h2 {
    font-size: 18px;
  }
}

.country-card {
  width: 520px;
  background: linear-gradient(#4882c4, #000);
  border-radius: 10px;
  padding: 10px;
  font-size: 20px;
}

.country-info {
  justify-content: center;
  display: flex;
}

.country-title-box {
  align-items: center;
  display: flex;
}

.country-flag {
  margin-right: 5px;
}

p {
  font-weight: 700;
}

.languages-item:not(:last-child):after {
  content: ",";
}

.country-title {
  font-weight: 400;
}

